<template>
    <main class="experiments">
        <ac-grid cols=12 align-h="center">
            <ac-col cols="11">
                <date-range-dropdown :callback="()=>{}" :route="{name:'Experiments',params:{id:this.params.id}}"  v-on:change="setTimeRange"></date-range-dropdown>
                <div v-if='experimentIDs'>
                    <div class="graph-row" v-for="(graph,i) in endpointGraphs" :key="i"  v-show="graph.data.visible">
                        <graph :data="graph.data" :layout="graph.layout" :spinner="graph.spinner" ></graph>
                    </div>
                </div>
                <ac-grid>
                    <ac-col 
                        cols="12"
                        md-cols="6"
                        lg-cols="4"
                        xl-cols="3"
                        v-for="(exp, i) in experimentDescriptions" 
                        :key="i">
                        <section class="experiment-section">
                            <h2 class="experiment-section-name">{{exp.name}}</h2>
                            <div class="experiment-section-description">{{exp.description}}</div>
                            <router-link :to="{name:'Experiments',params:{id:exp.name}}" class="experiment-section-link">View Graphs</router-link>
                        </section>
                    </ac-col>
                </ac-grid>
                <div v-if="experimentDescriptions.length==0">
                    <graph :spinner="{'show':true,message:'Fetching data...'}"></graph>
                </div>
            </ac-col>
        </ac-grid>
    </main>
</template>
<script type="text/javascript">
    import stats from "stats-lite"
    import graph from "@/components/graph";
    import dateRangeDropdown from "@/components/date-range-dropdown"
    import moment from "moment"
    import Color from "ac-colors"
    import graphSetup from "@/mixins/graph-setup.js";
    export default {
        mixins:[graphSetup],
        data(){
            return {
                graphColor:{
                    "primary":new Color({"type":"hex", "color":"#7FA44F"}),
                    "secondary":new Color({"type":"hex", "color":"#989998"})
                },
                timeRange:{toTime:null,fromTime:null},
            }
        },
        components:{
            "date-range-dropdown":dateRangeDropdown,
            "graph":graph,
        },
        computed:{
            params(){
                return this.$route.params;
            },
            query(){
                return this.$route.query;
            },
            rangeDropdownOptions(){
                return Object.values(this.rangeValues)
            },
            experimentIDs(){
                return (this.params.id!=null) ? [this.params.id] : null;
            },
            experimentDescriptions(){
                return this.$store.state.experimentDescriptions || [];
            }
        },
        methods:{
            setTimeRange(range){
                this.timeRange = range;
            },
            processLatencyTimeData(jsonDataRows){
                this.latencyTimeSpinner={show:true, message:"Sorting data..."};
                this.latencyTimeData.ready=false;
                this.latencyTimeLayout.ready=false;
                const latencyTimeResults = {}
                const addLatencyTimeRecord = (record, index)=>{
                    if(record){
                        if(!latencyTimeResults[index]){
                            latencyTimeResults[index] = []
                        }
                        // Time is in nanoseconds, convert to ms
                        const sendTime = record.end_stime/(10**6);
                        const recvTime = record.end_rtime/(10**6);
                        const latency = (recvTime-sendTime);
                        const timestamp = moment(sendTime,"x").utc().format("YYYY-MM-DD HH:mm:ss.SS");
                        // Verify data is properly parsed
                        if(!isNaN(latency)){
                            latencyTimeResults[index].push({
                                timestamp,
                                latency,
                                sendTime
                            });
                        }
                    }
                }
                let processedRows = 0;
                jsonDataRows.forEach((curr)=>{
                    if(curr.exp=="DNS_local"){ 
                        addLatencyTimeRecord(curr.data.rst_list[0], curr.index)
                    }else{
                        addLatencyTimeRecord(curr.data, curr.index);
                    }
                    this.latencyTimeSpinner = {
                        show:true,
                        message:`Sorting data (${Math.ceil(processedRows/jsonDataRows.length*100)}%)`
                    };
                    processedRows++;
                });
                this.latencyTimeSpinner.message="Generating graph...";
                this.latencyTimeData.data=[];
                const markerBorderWidth = 2;
                Object.keys(latencyTimeResults).forEach((index)=>{
                    // Sort records by sendTime
                    latencyTimeResults[index].sort((a,b)=>a.sendTime-b.sendTime)
                    const traceColor = Color.randomFromString(index+index+index);
                    this.latencyTimeData.data.push({
                        x:latencyTimeResults[index].map(record=>record.timestamp),
                        y:latencyTimeResults[index].map(record=>record.latency),
                        name:index,
                        mode:"lines+markers",
                        line:{
                            color:traceColor.rgbString
                        },
                        marker:{
                            color:"rgba(0,0,0,0)",
                            line:{
                                color:traceColor.rgbString,
                                width:markerBorderWidth
                            }
                        }
                    });
                })
                // Update yaxis range
                this.latencyTimeData.ready=true;
                this.latencyTimeLayout.ready=true;
                this.latencyTimeSpinner.show=false;
            },
            processLatencyHourlyData(jsonDataRows){
                this.latencyHourlySpinner={show:true,message:"Sorting data..."};
                this.latencyHourlyData.ready=false;
                this.latencyHourlyLayout.ready=false;
                const latencyHourlyResults = {}
                const addLatencyHourlyRecord = (record, index)=>{
                    if(record){
                        if(!latencyHourlyResults[index]){
                            latencyHourlyResults[index] = new Array(24).fill();
                            latencyHourlyResults[index] = latencyHourlyResults[index].map(()=>[]);
                        }
                        
                        // Time is in nanoseconds, convert to ms
                        const sendTime = record.end_stime/(10**6);
                        const recvTime = record.end_rtime/(10**6);
                        const latency = (recvTime-sendTime);
                        const hours = moment(sendTime,"x").utc().hours();
                        // Verify data is properly parsed
                        if(!isNaN(hours) && !isNaN(latency)){
                            latencyHourlyResults[index][hours].push(latency);
                        }
                    }
                }
                let processedRows = 0;
                jsonDataRows.forEach((curr)=>{
                    if(curr.exp=="DNS_local"){ 
                        addLatencyHourlyRecord(curr.data.rst_list[0], curr.index)
                    }else{
                        addLatencyHourlyRecord(curr.data, curr.index);
                    }
                    this.latencyHourlySpinner = {
                        show:true,
                        message:`Sorting data (${Math.ceil(processedRows/jsonDataRows.length*100)}%)`
                    };
                    processedRows++;
                });
                Object.keys(latencyHourlyResults).forEach((index)=>{
                    let prevLatency = null;
                    latencyHourlyResults[index] = latencyHourlyResults[index].map((hourData)=>{
                        const filteredHourData = hourData.filter((val)=>!!val);
                        if(filteredHourData.length==0){
                            return prevLatency;
                        }
                        const avgLatency = stats.mean(filteredHourData);
                        prevLatency = avgLatency;
                        return avgLatency; 
                    });
                });
                this.latencyHourlySpinner.message="Generating graph...";
                this.latencyHourlyData.data=[];
                const markerBorderWidth = 2;
                const utcHours = new Array(24).fill().map((v,i)=>i); 
                Object.keys(latencyHourlyResults).forEach((index)=>{
                    const traceColor = Color.randomFromString(index+index+index);
                    this.latencyHourlyData.data.push({
                        x:utcHours,
                        y:latencyHourlyResults[index],
                        name:index,
                        mode:"lines+markers",
                        line:{
                            color:traceColor.rgbString
                        },
                        marker:{
                            color:"rgba(0,0,0,0)",
                            line:{
                                color:traceColor.rgbString,
                                width:markerBorderWidth
                            }
                        }
                    })
                })
                // Update yaxis range
                this.latencyHourlyData.ready=true;
                this.latencyHourlyLayout.ready=true;
                this.latencyHourlySpinner.show=false;
            },

            processBandwidthTimeData(jsonDataRows){
                this.bandwidthTimeSpinner={show:true,message:"Sorting data..."};
                this.bandwidthTimeData.ready=false;
                this.bandwidthTimeLayout.ready=false;
                const bandwidthTimeResults = {}
                const addBandwidthTimeRecord = (data, index)=>{
                    bandwidthTimeResults[index] = bandwidthTimeResults[index] || {
                        "avail_band":[],
                        "btnk_band":[]
                    };
                    
                    // Time is in nanoseconds, convert to ms
                    const sendTime = data.ctrl_stime/(10**6);
                    const timestamp = moment(sendTime,"x").utc().format("YYYY-MM-DD HH:mm:ss.SS");

                    if(data.avail_band && data.btnk_band){
                        bandwidthTimeResults[index].avail_band.push({
                            // Bandwidth is in bps, convert to Mbps
                            "bandwidth":data.avail_band / (10**6),
                            timestamp,
                            sendTime
                        });
                        bandwidthTimeResults[index].btnk_band.push({
                            // Bandwidth is in bps, convert to Mbps
                            "bandwidth":data.btnk_band / (10**6),
                            timestamp,
                            sendTime
                        });
                    }
                    
                }
                let processedRows = 0;
                jsonDataRows.forEach((curr)=>{
                    addBandwidthTimeRecord(curr.data, curr.index);
                    this.bandwidthTimeSpinner = {
                        show:true,
                        message:`Sorting data (${Math.ceil(processedRows/jsonDataRows.length*100)}%)`
                    };
                    processedRows++;
                });
                this.bandwidthTimeSpinner.message="Generating graph...";
                this.bandwidthTimeData.data=[];
                const markerBorderWidth = 2;
                Object.keys(bandwidthTimeResults).forEach((index)=>{
                    const bandwidthTimeData = bandwidthTimeResults[index];
                    const traceColor = Color.randomFromString(index+index+index);
                    const altColor = new Color({'color':[traceColor.lchuv[0]+20,traceColor.lchuv[1],traceColor.lchuv[2]], type:'lchuv'})
                    const graphColors = [traceColor,altColor];
                    Object.keys(bandwidthTimeData).forEach((exp, i)=>{
                        // Sort records by sendTime
                        bandwidthTimeData[exp].sort((a,b)=>a.sendTime-b.sendTime)
                        const traceColor = graphColors[i] || Color.random();
                        this.bandwidthTimeData.data.push({
                            x:bandwidthTimeData[exp].map(record=>record.timestamp),
                            y:bandwidthTimeData[exp].map(record=>record.bandwidth),
                            name:index+" - "+exp,
                            mode:"lines+markers",
                            line:{
                                color:traceColor.rgbString
                            },
                            marker:{
                                color:"rgba(0,0,0,0)",
                                line:{
                                    color:traceColor.rgbString,
                                    width:markerBorderWidth
                                }
                            }
                        })
                    })
                })
                // Update yaxis range
                this.bandwidthTimeData.ready=true;
                this.bandwidthTimeLayout.ready=true;
                this.bandwidthTimeSpinner.show=false;
            },
            processBandwidthHourlyData(jsonDataRows){
                this.bandwidthHourlySpinner={show:true,message:"Sorting data..."};
                this.bandwidthHourlyData.ready=false;
                this.bandwidthHourlyLayout.ready=false;
                const bandwidthHourlyExperiments = this.$store.state.experiments.filter((exp)=>exp.categories.includes("bandwidth")).map(exp=>exp.name);
                const bandwidthHourlyResults = {}
                const addBandwidthHourlyRecord = (data)=>{
                    if(!bandwidthHourlyResults.avail_band){
                        bandwidthHourlyResults.avail_band = new Array(24).fill();
                        bandwidthHourlyResults.avail_band = bandwidthHourlyResults.avail_band.map(()=>[]);
                    }
                    if(!bandwidthHourlyResults.btnk_band){
                        bandwidthHourlyResults.btnk_band = new Array(24).fill();
                        bandwidthHourlyResults.btnk_band = bandwidthHourlyResults.btnk_band.map(()=>[]);
                    }
                    
                    // Time is in nanoseconds, convert to ms
                    const sendTime = data.ctrl_stime/(10**6);
                    const hours = moment(sendTime,"x").utc().hours();

                    if(data.avail_band && data.btnk_band){
                        // Bandwidth is in bps, convert to Mbps
                        bandwidthHourlyResults.avail_band[hours].push(data.avail_band / (10**6));
                        bandwidthHourlyResults.btnk_band[hours].push(data.btnk_band / (10**6));
                    }
                    
                }
                let processedRows = 0;
                jsonDataRows.forEach((curr)=>{
                    if(bandwidthHourlyExperiments.includes(curr.exp)){
                        addBandwidthHourlyRecord(curr.data);
                    }
                    this.bandwidthHourlySpinner = {
                        show:true,
                        message:`Sorting data (${Math.ceil(processedRows/jsonDataRows.length*100)}%)`
                    };
                    processedRows++;
                });
                const meanBandwidthHourlyResults = {}
                // Standard deviation data for error bars
                const stdevBandwidthHourlyResults = {}
                Object.keys(bandwidthHourlyResults).forEach((expName)=>{
                    let prevAvgBandwidth = null;
                    let prevStdevBandwidth = null;
                    meanBandwidthHourlyResults[expName] = bandwidthHourlyResults[expName].map((hourData)=>{
                        const filteredHourData = hourData.filter((val)=>!!val);
                        if(filteredHourData.length==0){
                            return prevAvgBandwidth;
                        }
                        const avgBandwidth =  stats.mean(filteredHourData);
                        prevAvgBandwidth = avgBandwidth;
                        return avgBandwidth; 
                    });
                    stdevBandwidthHourlyResults[expName] = bandwidthHourlyResults[expName].map((hourData)=>{
                        const filteredHourData = hourData.filter((val)=>!!val);
                        if(filteredHourData.length==0){
                            return prevStdevBandwidth;
                        }
                        const stdevBandwidth =  stats.sampleStdev(filteredHourData);
                        prevStdevBandwidth = stdevBandwidth;
                        return stdevBandwidth; 
                    });
                });
                this.bandwidthHourlySpinner.message="Generating graph...";
                this.bandwidthHourlyData.data=[];
                const markerBorderWidth = 2;
                const utcHours = new Array(24).fill().map((v,i)=>i); 
                Object.keys(meanBandwidthHourlyResults).forEach((exp,i)=>{
                    const traceColor = Object.values(this.graphColor)[i] || Color.random();
                    this.bandwidthHourlyData.data.push({
                        x:utcHours,
                        y:meanBandwidthHourlyResults[exp],
                        error_y: {
                            type: 'data',
                            array: stdevBandwidthHourlyResults[exp],
                            visible: true,
                        },
                        name:exp,
                        mode:"lines+markers",
                        line:{
                            color:traceColor.rgbString
                        },
                        marker:{
                            color:"rgba(0,0,0,0)",
                            line:{
                                color:traceColor.rgbString,
                                width:markerBorderWidth
                            }
                        }
                    })
                })
                // Update yaxis range
                this.bandwidthHourlyData.ready=true;
                this.bandwidthHourlyLayout.ready=true;
                this.bandwidthHourlySpinner.show=false;
            },
            processRTTTimeData(jsonDataRows){
                this.rttTimeSpinner={show:true,message:"Sorting data..."};
                this.rttTimeData.ready=false;
                this.rttTimeLayout.ready=false;
                const rttTimeResults = {}
                const addRTTTimeRecord = (record, index)=>{
                    if(record){   
                        // Time is in nanoseconds, convert to ms
                        const sendTime = record.ctrl_stime/(10**6);
                        const recvTime = record.ctrl_rtime/(10**6);
                        const rtt = (recvTime-sendTime);
                        const timestamp = moment(sendTime,"x").utc().format("YYYY-MM-DD HH:mm:ss.SS");
                        // Verify data is properly parsed
                        if(!isNaN(rtt)){
                            if(!rttTimeResults[index]){
                                rttTimeResults[index] = [];
                            }
                            rttTimeResults[index].push({
                                timestamp,
                                rtt,
                                sendTime
                            });
                        }
                    }
                }
                let processedRows = 0;
                jsonDataRows.forEach((curr)=>{
                    if(curr.exp=="DNS_local"){ 
                        addRTTTimeRecord(curr.data.rst_list[0], curr.index);
                    }else{
                        addRTTTimeRecord(curr.data, curr.index);
                    }
                    this.rttTimeSpinner = {
                        show:true,
                        message:`Sorting data (${Math.ceil(processedRows/jsonDataRows.length*100)}%)`
                    };
                    processedRows++;
                });
                this.rttTimeSpinner.message="Generating graph...";
                this.rttTimeData.data=[];
                const markerBorderWidth = 2;
                // Sort records by sendTime
                Object.keys(rttTimeResults).forEach((index)=>{
                    const traceColor = Color.randomFromString(index+index+index);
                    const rttTimeData = rttTimeResults[index];
                    rttTimeData.sort((a,b)=>a.sendTime-b.sendTime);
                    this.rttTimeData.data.push({
                        x:rttTimeData.map(record=>record.timestamp),
                        y:rttTimeData.map(record=>record.rtt),
                        name:index,
                        mode:"lines+markers",
                        line:{
                            color:traceColor.rgbString
                        },
                        marker:{
                            color:"rgba(0,0,0,0)",
                            line:{
                                color:traceColor.rgbString,
                                width:markerBorderWidth
                            }
                        }
                    })
                });
                // Update yaxis range
                this.rttTimeData.ready=true;
                this.rttTimeLayout.ready=true;
                this.rttTimeSpinner.show=false;
            },
            processRTTHourlyData(jsonDataRows){
                this.rttHourlySpinner={show:true,message:"Sorting data..."};
                this.rttHourlyData.ready=false;
                this.rttHourlyLayout.ready=false;
                const rttHourlyResults = {}
                const addRTTHourlyRecord = (record, index)=>{
                    if(!rttHourlyResults[index]){
                        rttHourlyResults[index] = new Array(24).fill();
                        rttHourlyResults[index] = rttHourlyResults[index].map(()=>[]);
                    }
                    if(record){
                        // Time is in nanoseconds, convert to ms
                        const sendTime = record.ctrl_stime/(10**6);
                        const recvTime = record.ctrl_rtime/(10**6);
                        const rtt = (recvTime-sendTime);
                        const hours = moment(sendTime,"x").utc().hours();
                        // Verify data is properly parsed
                        if(!isNaN(hours) && !isNaN(rtt)){
                            rttHourlyResults[index][hours].push(rtt);
                        }
                    }
                }
                let processedRows = 0;
                jsonDataRows.forEach((curr)=>{
                    if(curr.exp=="DNS_local"){ 
                        addRTTHourlyRecord(curr.data.rst_list[0], curr.index)
                    }else{
                        addRTTHourlyRecord(curr.data, curr.index);
                    }
                    this.rttHourlySpinner = {
                        show:true,
                        message:`Sorting data (${Math.ceil(processedRows/jsonDataRows.length*100)}%)`
                    };
                    processedRows++;
                });
                let prevAvgRTT = null;
                let prevStdevRTT = null;
                console.log(rttHourlyResults);
                this.rttHourlySpinner.message="Generating graph...";
                this.rttHourlyData.data=[];
                const markerBorderWidth = 2;
                const utcHours = new Array(24).fill().map((v,i)=>i); 
                Object.keys(rttHourlyResults).forEach((index)=>{
                    const rttHourlyData = rttHourlyResults[index];
                    const meanRTTHourlyData= rttHourlyData.map((hourData)=>{
                    const filteredHourData = hourData.filter((val)=>!!val);
                        if(filteredHourData.length==0){
                            return prevAvgRTT;
                        }
                        const avgRTT = stats.mean(filteredHourData);
                        prevAvgRTT = avgRTT;
                        return avgRTT; 
                    });
                    const stdevRTTHourlyData = rttHourlyData.map((hourData)=>{
                        const filteredHourData = hourData.filter((val)=>!!val);
                        if(filteredHourData.length==0){
                            return prevStdevRTT;
                        }
                        const stdevRTT =  stats.sampleStdev(filteredHourData);
                        prevStdevRTT = stdevRTT;
                        return stdevRTT; 
                    });
                    const traceColor = Color.randomFromString(index+index+index);
                    this.rttHourlyData.data.push({
                        x:utcHours,
                        y:meanRTTHourlyData,
                        error_y: {
                            type: 'data',
                            array: stdevRTTHourlyData,
                            visible: true,
                        },
                        name:index,
                        mode:"lines+markers",
                        line:{
                            color:traceColor.rgbString
                        },
                        marker:{
                            color:"rgba(0,0,0,0)",
                            line:{
                                color:traceColor.rgbString,
                                width:markerBorderWidth
                            }
                        }
                    })
                })
                // Update yaxis range
                this.rttHourlyData.ready=true;
                this.rttHourlyLayout.ready=true;
                this.rttHourlySpinner.show=false;
            },
            initGraphData(){
                const spinnerList = [
                    this.latencyTimeSpinner,
                ]
                const graphList = [
                    {
                        f:this.processLatencyTimeData,
                        data:this.latencyTimeData,
                        category:'latency'
                    },
                    {
                        f:this.processLatencyHourlyData,
                        data:this.latencyHourlyData,
                        category:'latency'
                    },
                    {
                        f:this.processBandwidthTimeData,
                        data:this.bandwidthTimeData,
                        category:'bandwidth'
                    },
                    {
                        f:this.processBandwidthHourlyData,
                        data:this.bandwidthHourlyData,
                        category:'bandwidth'
                    },
                    {
                        f:this.processRTTTimeData,
                        data:this.rttTimeData,
                        category:'rtt'
                    },
                    {
                        f:this.processRTTHourlyData,
                        data:this.rttHourlyData,
                        category:'rtt'
                    },
                ]
                spinnerList.forEach((spinner)=>{
                    spinner.show=true;
                    spinner.message="Fetching data...";
                })
                let {toTime,fromTime}=this.timeRange;
                // Default fromTime is 0
                fromTime = (fromTime!=null) ? fromTime : 0;
                // Default toTime is current time
                toTime = (toTime!=null) ? toTime : parseFloat(moment().format("X"));
                this.$store.dispatch('loadData', fromTime).then(()=>{
                    // Get graph colors
                    this.$store.state.experiments.forEach((exp)=>{
                        this.graphColor[exp.name] = new Color({"type":"hex", "color":exp.color});
                    })
                    const jsonDataRows = this.$store.state.data.filter((row, i)=>{
                        const endpoints = this.$store.state.endpoints.reduce((acc,curr)=>{
                            const id = "M"+curr.monitor;
                            const shortDescription = id+" - "+curr.description.split(/[\s_]/)[0]
                            acc[id]={
                                "shortDescription":shortDescription,
                                ...curr
                            };
                            return acc;
                        }, {});
                        if(row.monitor){
                            const index = "M"+row.monitor; 
                            row.index = endpoints[index].shortDescription;
                            spinnerList.forEach((spinner)=>{
                                spinner.show=true;
                                spinner.message=`Filtering data (${Math.ceil(i/this.$store.state.data.length*100)}%)`

                            })
                            return (this.experimentIDs==null || 
                                (Array.isArray(this.experimentIDs) && this.experimentIDs.includes(row.exp))
                            )
                            && (
                                (row.start >= fromTime && row.start <= toTime) ||
                                (row.end>=fromTime && row.end<=toTime)
                            )
                        }else{
                            return false;
                        }
                    });
                    if(this.experimentIDs){
                        graphList.forEach((graph)=>{
                            const currentExp = this.$store.state.experiments.find((exp)=>this.experimentIDs.includes(exp.name));
                            if(currentExp.categories.includes(graph.category)){
                                graph.data.visible = true;
                                graph.f(jsonDataRows);
                            }else{
                                graph.data.visible = false;
                            }
                        });  
                    }
                })
            }
        },
        watch:{
            experimentIDs:{
                immediate:true,
                deep:true,
                async handler(){   
                    await this.$nextTick();
                    this.initGraphData();
                }
            },
        }
    }
</script>
<style lang='scss' scoped>
    .endpoint-summary{
        background-color: #FFF;
        box-shadow: 3px 3px 5px rgba(0,0,0,0.1);
    }
    .experiment-section{
        background-color: #FFF;
        width: 90%;
        margin: 5% 0;
        box-sizing: border-box;
        padding: 2em;
        box-shadow: 3px 3px 5px rgba(0,0,0,0.1);  
        line-height: 2em;
        text-align: left; 
    }
    .experiment-section-link{
        color: #615E8A;
        font-weight: 600;
        text-transform: uppercase;
        letter-spacing: 0.75px;
        margin-top: 1em;
        display: block;
        text-decoration: none;
    }
    .graph-row{
        margin: 2% 0;
        .summary-key,
        .summary-value{
            padding: 2%;
        }
        .summary-section + .summary-section{
            .summary-key,
            .summary-value{
                border-left: solid 1px rgba(0,0,0,0.1);
            }
        }
        .summary-key{
            background-color: #615E8A;
            color: #FFF;
            font-weight: bold;
        }
    }
</style>